
/*  Forms */
/* ==================================================== */


form {
	@extend %clearfix;
	@include copy(regular);
	position: relative;
	width: 100%;
	display: block;
	background: transparent;
}



fieldset {
	width: 100%;
	display: block;
	margin: 0 auto;
	padding: 0;
	font-size: 1rem;
	background: transparent;
	border: 0 none;
	@extend %clearfix;

	&:not(:first-of-type) {
		margin-top: calc(#{$space-tiny} * 1.75);

		&.column-3 {
		}
	}
}


label {
	display: block;
	margin: 0 0 0.751rem;
	padding: 0;
	@include font-size(18px);
	@include font-size(16px);
	@include line-height(1.1);
	text-transform: capitalize;
	color: $grey;
	color: lighten($blue, 2.5%);

	&.radio-check {
		display: inline;
		font-weight: normal;
	}

	span {
		color: $blue;
		// font-weight: bold;
		font-size: 1.25rem;
	}
}


input[type="text"],
input[type="email"],
input[type="password"],
input[type="tel"],
textarea,
select {
	@include font-size(16px);
	@include letter-spacing(tiny);
	position: relative;
	//display: block;
	width: 100%;
	padding: 0.75rem 1rem;
	//color: $grey;
	background: $white;
	appearance: none;
	border: 0 none;
	outline: 0 none;
	border: 1px solid $sky;
	border-radius: 2px;

	&:hover {
		color: $sky;
		border: 1px solid $green-dk;
	}

	&:focus {
		color: $blue;
		border: 1px solid $blue-dk;
	}

	&::placeholder {
		color: $dark-grey;
		opacity: 1;
	}
}


textarea {
	height: 10rem;
}


select {
	min-width: 25%;
	// width: auto;
	margin: 0;
	border-radius: 3px;
}


option {
	padding: 0.25rem 0 0;
	border: 0 none;
	outline: 0 none;
}


fieldset.radio ul {
	@extend %clearfix;
	margin-top: 0.25rem;
	list-style-type: none;
}


fieldset.radio ul li {
	float: left;
	margin: 0 2rem 0.25rem 0;
}


fieldset.radio ul li:last-child {
	margin: 0;
}


p.form-help {
	display: inline-block;
	margin: 0.125rem 0 0.25rem;
	@include font-size(15px);
	color: #999;
}

p.form-data {
	display: inline-block;
	margin: 0.125rem 0 0.75rem;
	@include font-size(18px);
}


input[type="submit"] {
	appearance: none;
	@include border(0, solid, transparant);
	@include outline(0, solid, transparant);
	cursor: pointer;

	&:hover,
	&:focus {
	}

	&:active {
	}
}
img.ajax-loader {
	max-height: 16px;
	max-width: 16px;
	display: none;
}
//table filter
.table-filter {
	@extend %column;
	justify-content: space-between;
	flex-wrap: nowrap;
	flex-direction: row;
	align-items: center;
	padding: calc(#{$space-tiny} * 2) 0 calc(#{$space-tiny} * 0.5) 0;
	.selector {
		position: relative;
		width: 100%;
		flex: 1;
		margin-right: 1.125rem;
		// flex: 2;
		// margin-bottom: 1rem;
		@include break-point(mobile) {
			width: 100%;
		}
		@include break-point(largelaptop) {
			display: inline-block;
			width: auto;
		}
		@include break-point(desktop) {
			width: auto;
		}
	 &:after {
			top: 45%;
			left: 85%;
			border: solid transparent;
			content: " ";
			height: 0;
			width: 0;
			position: absolute;
			pointer-events: none;
			border-color: rgba(0, 0, 0, 0);
			border-top-color: $sky;
			border-width: 0.5rem;
			// margin-top: -0.2rem;
			z-index: 2;

			@include break-point(tablet) {
				width: auto;
				left: 96%;
			}
			@include break-point(desktop) {
				width: auto;
				left: 85%;
			}
			// @include break-point(largelaptop) {
			// 	// top:-27%;
			// 	left: 86%;
			// 	position: relative;
			// }
			// @include break-point(desktop) {
			// 	top:0;
			// 	width: 100%;
			// 	// left: 90%;
			// }
		}


	 .dropdown {
			// @extend %column-5;
			// @extend %column-5;

			color: lighten($dark-grey, 10%);
			//display: inline-block;
			width: 100%;
			padding: calc(#{$space-tiny} * 0.7);
			padding-right: calc(#{$space-tiny} * 2);

			// padding-right: calc(#{$space-tiny} * 3);
			@include break-point(mobile) {
				display: block;
				width: 100%;
			}
			@include break-point(desktop) {
				width: 100%;
				max-width: 185px;
			}

			option{
				padding-right: calc(#{$space-tiny} * 3);
				color: $sky;
				&:first-child {
					padding-left: calc(#{$space-tiny} * 1);
				}
				&:not(:checked) {
					// color: lighten($dark-grey, 10%);
				}
			}
			&:focus {
				outline: none;
			}
	}

}
.button {
	margin-top: 0;
	flex: 1;
	@include break-point(tablet) {
		margin-top: 1rem;
	}
}

}
