
/*  Flags */
/* ==================================================== */


.flag-icon-background {
  background-size: contain;
  background-position: 50%;
  background-repeat: no-repeat;
}

.flag-icon {
  background-size: contain;
  background-position: 50%;
  background-repeat: no-repeat;
  position: relative;
  display: inline-block;
  width: 1.33333333em;
  line-height: 1em;
}

.flag-icon:before {
  content: "\00a0";
}

.flag-icon.flag-icon-squared {
  width: 1em;
}

.flag-icon-ke {
  background-image: url(/wp-content/themes/novapioneer/img/flags/4x3/ke.svg);
}
.flag-icon-ke.flag-icon-squared {
  background-image: url(/wp-content/themes/novapioneer/img/flags/1x1/ke.svg);
}


.flag-icon-sa {
  background-image: url(/wp-content/themes/novapioneer/img/flags/4x3/sa.svg);
}
.flag-icon-sa.flag-icon-squared {
  background-image: url(/wp-content/themes/novapioneer/img/flags/1x1/sa.svg);
}


.flag-icon-za {
  background-image: url(/wp-content/themes/novapioneer/img/flags/4x3/za.svg);
}
.flag-icon-za.flag-icon-squared {
  background-image: url(/wp-content/themes/novapioneer/img/flags/1x1/za.svg);
}


