

.card-container {
	@include flexbox();
	@include flex-direction(row);
	@include flex-wrap(wrap);
	margin: auto;
	width: 70%;
	justify-content: space-around;
	// margin-bottom: -4rem;

	&.team-profile-container {
		width: 100%;
	}

	&.steps-container {
		width: 100%;
	}

	&.all-contacts-container {
		width: 100%;
	}
}


.card {
	width: 40%;
	// margin-bottom: 5rem;
	text-align: center;
	
	h1 {
		text-align: center;
		font-size: inherit;
		line-height: 1.75;
	}

	h2 {
		text-align: center;
		@include copy(semi);
    	@include font-size(28px);
    	color: $green-blue;
	}

	p {
		// @include font-size(18px);
		// line-height: 1.5;
	}

	&.member-profile {
		width: 25%;

		h2 {
			@include copy(semi);
    		@include font-size(18px);
		}

		.member-name {
		 	@include copy(semi);
		 	@include font-size(26px);
		 	color: $green-blue;
		 	text-shadow: none;
		 	text-align: center;
		}
	}

	// &.admission-step {
	// 	width: unset;
	// 	@extend %column-3;

 //    	@include break-point(laptop) {
	// 		&:not(:first-of-type) {
	//     		margin-left: calc(#{$space-tiny} * 2);
	//     	}
	//     }
	// }

	&.school-contact {
		width: 30%;
		padding-right: calc(#{$space-tiny} * 1);
    	padding-left: calc(#{$space-tiny} * 1);

    	.kenya-schools {
    		width: 40%;
    	}
	}
}

.card figure {
	img {
		margin: 0 auto 1.5rem auto;
		width: auto;
	}
}


